window.$ = window.jQuery = function (selectorOrArrayOrTemplate) {
  //‰∏§‰∏™ = Âú®‰∏ÄË°åÔºåÊâßË°åÈ°∫Â∫è‰∏∫‰ªéÂè≥ÂæÄÂ∑¶
  let elements;
  if (typeof selectorOrArrayOrTemplate === "string") {
    if (selectorOrArrayOrTemplate[0] === "<") {
      elements = [createElement(selectorOrArrayOrTemplate)];
    } else {
      elements = document.querySelectorAll(selectorOrArrayOrTemplate);
      //Ëé∑ÂèñelementsÂêéÂèØ‰ª•Áõ¥Êé•Ë∞ÉÁî®fnÊìç‰Ωúelements ÊâÄ‰ª• jQuery ‰ºöreturn ‰∏Ä‰∏™ÂØπË±°,ËÄå‰∏çÊòØelements
    }
  } else if (selectorOrArrayOrTemplate instanceof Array) {
    elements = selectorOrArrayOrTemplate;
  }

  function createElement(string) {
    const container = document.createElement("template");
    container.innerHTML = string.trim();
    return container.content.firstChild;
  }

  const api = Object.create(jQuery.prototype);

  Object.assign(api, {
    elements: elements,
    oldApi: selectorOrArrayOrTemplate.oldApi,
    //end() ËøîÂõûÁöÑÊòØapiÔºåapiÊòØÊ≤°ÊúâoldApiÁöÑÔºåÊâÄ‰ª•ÈúÄË¶ÅÊûÑÈÄ†‰∏Ä‰∏™
  });
  return api;
};

jQuery.fn = jQuery.prototype = {
  constructor: jQuery,
  jquery: true,
  addClass(className) {
    for (let i = 0; i < this.elements.length; i++) {
      this.elements[i].classList.add(className);
      //ËøôÈáåÊòØ‰∏Ä‰∏™Èó≠ÂåÖÔºåaddClassÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÁöÑelementsÔºàÊ≠§Êó∂ÊµèËßàÂô®‰∏ç‰ºöÊ∏ÖÈô§elementsÔºåÂõ†Ê≠§ÂèØ‰ª•ÂÆûÁé∞ÈìæÂºèÊìç‰ΩúÔºâ
    }
    return this;
    //return this ÈìæÂºèÊìç‰ΩúÔºåÊñπ‰æøÂ§öÊ¨°Ë∞ÉÁî®Ëá™Â∑±
    /*
      Ë∞ÉÁî®ËØ•ÂáΩÊï∞ api.addClass(x) === api.addClass.call(api,x)
     */
  },

  find(selector) {
    let array = [];
    for (let i = 0; i < this.elements.length; i++) {
      const elements2 = Array.from(this.elements[i].querySelectorAll(selector));
      //ÊâæÂà∞ÊúâselectorÁöÑÊâÄÊúâÂÖÉÁ¥†ÔºàquerySelectorÂæóÂà∞ÁöÑÊòØ‰º™Êï∞ÁªÑ,Áõ¥Êé•concat‰ºöÊääelements2ÂÖ®ÈÉ®ÊîæÂà∞‰∏Ä‰∏™keyÈáåÈù¢Ôºâ
      array = array.concat(elements2);
    }
    array.oldApi = this;
    //Áî±‰∫éËøîÂõû‰∏ä‰∏Ä‰∏™api
    return jQuery(array);
  },

  end() {
    return this.oldApi;
  },
  //ËøîÂõû‰∏ä‰∏Ä‰∏™api

  each(fn) {
    for (let i = 0; i < this.elements.length; i++) {
      fn.call(null, this.elements[i], i);
    }
    return this;
  },

  parent() {
    const array = [];

    this.each((node) => {
      if (array.indexOf(node.parentNode) === -1) {
        //Èò≤Ê≠¢Â§öÊ¨°pushÈáçÂ§çÁöÑparentNode
        array.push(node.parentNode);
      }
    });
    return jQuery(array);
    //ËøîÂõûÁöÑÊòØÂèØÊìç‰ΩúÁöÑÂØπË±°
  },

  children() {
    const array = [];
    this.each((node) => {
      if (array.indexOf(node.children) === -1) {
        array.push(...node.children);
        //... Â±ïÂºÄÊìç‰ΩúÁ¨¶
      }
    });
    return jQuery(array);
  },

  print() {
    console.log(this.elements);
  },

  appendTo(node) {
    //ÊääxxxËøΩÂä†Âà∞node‰∏≠
    if (node instanceof Element) {
      this.each((el) => node.appendChild(el));
    } else if (node.jquery === true) {
      this.each((el) => node.get(0).appendChild(el));
    }
  },

  get(index) {
    return this.elements[index];
  },
};
